import { Component, AfterViewInit } from '@angular/core';
import { Chart } from 'chart.js'
import { AuthService } from '../../services/auth.service'
import {FlashMessagesService} from 'angular2-flash-messages';



@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements AfterViewInit {
  moods = [];
  dates = [];
  constructor(
    private authService: AuthService,
    private flashMessage: FlashMessagesService
  ) { }

  canvas: any;
  // ctx: any;

  ngAfterViewInit() {
    const mood = [{ currentMood: 1, time: Date.now() },
    { currentMood: 1, time: Date.now() + (1000 * 60 * 60 * 24) },
    { currentMood: 4, time: Date.now() + ((1000 * 60 * 60 * 24) * 2) },
    { currentMood: 3, time: Date.now() + ((1000 * 60 * 60 * 24) * 3) },
    { currentMood: 2, time: Date.now() + ((1000 * 60 * 60 * 24) * 4) }]

    for (var key in mood) {
      this.moods.push(mood[key].currentMood);
      let jsdate = new Date(mood[key].time)
      this.dates.push(jsdate.toLocaleTimeString('en', { month: 'long', day: 'numeric' }))
    }

    var canvas = <HTMLCanvasElement>document.getElementById('myChart');
    var ctx: CanvasRenderingContext2D = canvas.getContext("2d");
    var myChart = new Chart(ctx, {
      "type": "line",
      "data": {
        "labels": this.dates,
        "datasets": [{
          "label": "Your Mood", "data": this.moods,
          "fill": false,
          "borderColor": "rgb(75, 192, 192)"
        }]
      },
      options: {
        responsive: false
      }
    });

  }

  onRegisterSubmit() {
    const User = JSON.parse(localStorage.getItem('user'));
    const mood = {
      userId: User.id,
      date: Date.now,
      weather: {       
        cloudCover: 0.7,
        rain: 60,
        temp: 3,
        summary: "shitty enuf"},
      sleep: 8,
      diet: "fair",
      exercise: "fair",
      moodData: [{ currentMood: 1, time: Date.now() },
        { currMood: 1, date: Date.now() + (1000 * 60 * 60 * 24) },
        { currMood: 4, date: Date.now() + ((1000 * 60 * 60 * 24) * 2) },
        { currMood: 3, date: Date.now() + ((1000 * 60 * 60 * 24) * 3) },
        { currMood: 2, date: Date.now() + ((1000 * 60 * 60 * 24) * 4) }]
    }

    // // Register user
    this.authService.setMood(mood).subscribe(data => {
      if (data.success) {
        this.flashMessage.show('You are now registered and can log in', { cssClass: 'alert-success', timeout: 3000 });
      } else {
        this.flashMessage.show('Something went wrong', { cssClass: 'alert-danger', timeout: 3000 });
            }
    });

  }

}



